@import url('https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');
*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  background: linear-gradient(to right, #ded1d1, #cfd7e2);
}
/* font-family: 'Nunito', sans-serif;
font-family: 'Playfair Display', serif;
font-family: 'Poppins', sans-serif; */

/* navbar css */
.navbar {
  font-family: "Poppins", sans-serif;
  font-size: 17px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 8px 6px -6px gray;
  --webkit-box-shadow: 0 8px 6px -6px rgb(75, 65, 65);
  border-bottom: solid rgb(94, 84, 84) !important;
  background: linear-gradient(to right, #ec7373, #9bc3f7);
}

.nav-link {
  font-weight: 300 !important;
}

.active {
  border-bottom: 2px solid black;
}

.navbar-brand {
  font-weight: 700;
  font-family: "roboto", sans-serif;
  letter-spacing: 3px;
}
/* page not found css */
.pnf {
  display: flex;
  min-height: 65vh;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.pnf-title {
  font-size: 50px;
  font-weight: 700;
}

.pnf-heading {
  font-weight: normal;
}

.pnf-btn {
  color: black;
  border: 1px solid black;
  text-decoration: none;
  padding: 10px;
  margin-top: 10px;
}
.pnf-btn:hover {
  background-color: black;
  color: white;
}

/*contact us css */
.contactus {
  margin: 0;
  padding: 0;
  height: 70vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
.bg{
  background: linear-gradient(to right, #ec7373, #9bc3f7);
}


/* footer css */
.footer {
  color: white;
  padding: 10px 25px;
  box-shadow: 8px 0px 6px -6px gray;
  --webkit-box-shadow: 8px 0px 6px -6px rgb(75, 65, 65);
  border-top: solid rgb(94, 84, 84) !important;
  background: linear-gradient(to right, #e20909, #000000);
}

.footer a {
  text-decoration: none;
  color: white;
  padding: 10px;
}

.footer a:hover {
  color: #ffefba;
  border-bottom: 1px solid #ffefba;
}

.product-link {
  text-decoration: none !important;
  color: black !important;
}
